.gpt3__footer {
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: var(--color-footer);
}

.gpt3__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.gpt3__footer-heading h1 {
    font-size: 4rem;
    line-height: 5rem;
    text-align: center;
    letter-spacing: -0.04;
    font-family: var(--font-family);
}

.gpt3__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    padding: 1rem;
    border: 1px solid white;
    margin-bottom: 10rem;
}

.gpt3__footer-btn p {
    box-sizing: border-box;
    font-family: var(--font-family);
    font-size: 1.1rem;
    line-height: 1.3rem;
    color: white;
    word-spacing: 2px;
}

.gpt3__footer-links {
    display: flex;
    justify-content: space-between;
    align-items: start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
    ;
}

.gpt3__footer-links div {
    width: 250px;
    margin: 1rem;
}

.gpt3__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.gpt3__footer-links_logo img {
    width: 118px;
    height: 30px;
    margin-bottom: 1rem;
}

.gpt3__footer-links_logo p {
    font-family: var(-- --font-family);
    font-size: .7rem;
    color: white;
    line-height: .8rem;
}

.gpt3__footer-links_div {
    display: flex;
    justify-content: start;
    flex-direction: column;
    ;
}

.gpt3__footer-links_div h4 {
    font-family: var(--font-family);
    font-size: .9rem;
    line-height: 1rem;
    color: white;
}

.gpt3__footer-links_div p {
    font-family: var(--font-family);
    font-size: .7rem;
    line-height: .8rem;
    color: white;
    margin: 0.5rem 0;
}

.gpt3__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.gpt3__footer-copyright p {
    font-family: var(--font-family);
    font-size: .8rem;
    line-height: .9rem;
    color: white;
}