.gpt3__features {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.gpt3__features-heading {
    flex: 1;
    display: flex;
    justify-content: start;
    align-items: start;
    flex-direction: column;
    text-align: left;
    margin-right: 5rem;
}

.gpt3__features-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 2.1rem;
    line-height: 3rem;
}

.gpt3__features-heading p {
    font-size: 1rem;
    font-family: var(--font-family);
    font-weight: 500;
    line-height: 2rem;
    color: var(--color-subtext);
    margin-top: 2rem;
}

.gpt3__features-container {
    flex: 1.5;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width:990px) {
    .gpt3__features {
        flex-direction: column;
    }
    .gpt3__features-heading {
        margin: 0 0 2rem 0;
    }
}

