.gpt3__possibility {
    display: flex;
    flex-direction: row;
}

.gpt3__possibility-image {
    flex: 1;
    display: flex;
    justify-content: start;
    align-items: center;
    margin: 2rem;
}

.gpt3__possibility-image img {
    width: 100%;
}

.gpt3__possibility-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-start;
    margin: 3rem 7rem 5rem 0;
}

.gpt3__possibility-content h4 {
    font-family: var(--font-family);
    font-size: 1rem;
    font-weight: 500;
    line-height: 2rem;
    color: #71e5ff;
}

.gpt3__possibility-content h4:last-child {
    color: var(--color-subtext);
    margin-bottom: 2rem;
}

.gpt3__possibility-content h1 {
    font-family: var(--font-family);
    font-size: 2.1rem;
    font-weight: 800;
    line-height: 3rem;
    margin: 1rem 0;
}

.gpt3__possibility-content p {
    font-family: 'Manrope';
    font-weight: 400;
    font-size: 1rem;
    line-height: 2rem;
    color: var(--color-text);
    margin-bottom: 2rem;
}

@media (max-width: 950px) {
    .gpt3__possibility {
        flex-direction: column;
        ;
    }
    .gpt3__possibility-image {
        margin: 1rem;
    }
    .gpt3__possibility-content {
        margin-top: 2rem;
    }
}