.gpt3__cta {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    background: var(--gradient-bar);
    border: 1px solid #000000;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    border-radius: .75rem;
    max-width: 80rem;
    min-height: 10.5rem;
    margin: auto;
    padding: 2rem;
}

.gpt3__cta-content p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: .75rem;
    line-height: 2rem;
}

.gpt3__cta-content h3 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 1.5rem;
    line-height: 3rem;
    color: black
}

.gpt3__cta-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-button button {
    font-family: 'Manrope';
    font-style: normal;
    font-weight: 700;
    font-size: 1.1rem;
    line-height: 2rem;
    color: #FFFFFF;
    background-color: black;
    padding: .9rem 2.5rem;
    border-radius: 2.2rem;
    border: none;
    outline: none;
    min-width: 150px;
}

@media screen and (max-width:950px) {
    .gpt3__cta {
        flex-direction: column;
        margin: auto 50px;
    }
    .gpt3__cta-button {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width:550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }
}