.gpt3__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;
    margin: 1rem;
}

.gpt3__features-container__feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.gpt3__features-container__feature-title h1 {
    font-size: 1.5rem;
    font-family: var(--font-family);
    font-weight: 800;
    line-height: 2rem;
    color: white;
    letter-spacing: -0.04em;
}

.gpt3__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background-image: var(--gradient-bar);
    box-shadow: 0 dpx 4px rgba(0, 0, 0, 0.2);
    margin-bottom: 0.75rem;
    border-radius: 2px;
}

.gpt3__features-container-feature__text {
    flex: 2;
    max-width: 900px;
    display: flex;
}

.gpt3__features-container-feature__text p {
    font-family: var(--font-family);
    font-size: 1.2rem;
    line-height: 2rem;
    color: var(--color-text);
    margin: 0.2rem 0;
    font-weight: 500;
}

@media screen and ( max-width: 550px) {
    html {
        font-size: 10px;
    }
}

@media screen and ( max-width: 550px) {
    html {
        font-size: 12px;
    }
}